.mobile-nav {
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.67);
	width: 100%;
	height: 100%;
	z-index: 20;
}

.shadow {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 1;
}

.shadow:hover {
	cursor: pointer;
}

.mobile-nav ul {
	position: relative;
	height: 100%;
	width: 70%;
	background-color: white;
	margin: 0;
	padding: 0;
	z-index: 2;
	box-shadow: 2px 0px 7px 7px rgba(0, 0, 0, 0.502);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px 20px;
	box-sizing: border-box;
}

.mobile-nav .logo {
	font-size: 36px;
	padding-bottom: 35px;
}

.line {
	width: 100%;
	height: 1px;
	background-color: black;
	margin-bottom: 35px;
}

.mobile-nav ul a {
	font-size: 24px;
	margin-bottom: 35px;
	text-transform: uppercase;
	font-weight: 400;
}

.mobile-nav ul a:hover {
	cursor: pointer;
	opacity: 0.8;
}
