.products {
	width: 100%;
	box-sizing: border-box;
	/* display: flex;
	flex-direction: row;
	flex-wrap: wrap;

	justify-content: space-evenly; */
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
	grid-gap: 10px;
	padding-bottom: 1rem;
	max-width: 1200px;
}

.products > div {
	margin: 20px 30px;
	margin-bottom: 80px;
	overflow: visible;
}

.product-image {
	display: flex;
	flex-direction: column;
	justify-content: center;

	position: relative;
	width: 180px;
	height: auto;
	box-shadow: 3px 3px 7px 3px rgba(0, 0, 0, 0.771);
}

.product-image img {
	width: 100%;
	height: auto;
}

.product-image-label {
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	color: rgb(255, 255, 255);
	font-size: 32px;
	font-weight: 400;
	text-transform: uppercase;
	width: 100%;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;
}

.image-shadow {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.45);
	display: none;
}

.label-2 {
	width: 100%;
	display: flex;
	justify-content: center;
	font-size: 24px;
	font-weight: 500;
	text-transform: uppercase;
	margin-top: 13px;
	position: absolute;
	bottom: -40px;
	color: black;
}

.product-image:hover {
	opacity: 0.92;
	cursor: pointer;
}

.product-image:hover .image-shadow {
	display: flex;
}

.product-image:hover .product-image-label {
	display: flex;
}

