header {
	width: 100%;
	min-height: 330px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	padding-bottom: 90px;
	box-sizing: border-box;
}

.main-nav {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 0;
	/* margin-top: 10px; */
}

.main-nav li {
	margin-left: 45px;
	margin-right: 45px;
	font-size: 27px;
	font-weight: 400;
	text-transform: uppercase;
}

.main-nav li:hover {
	opacity: 0.8;
	cursor: pointer;
}

.logo {
	font-size: 64px;
	font-weight: 900;
}

.hamburger {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 24px;
	display: none;
	align-items: center;
	width: 70px;
}

.hamburger > div {
	width: 100%;
	height: 3px;
	background-color: black;
}

.hamburger > div:nth-child(2) {
	width: 60%;
	height: 2px;
}

.hamburger:hover {
	cursor: pointer;
	opacity: 0.8;
}

@media only screen and (max-width: 1400px) {
	.main-nav li {
		/* display: none; */
		font-size: 22px;
		margin-left: 30px;
		margin-right: 30px;
	}
}

@media only screen and (max-width: 940px) {
	.main-nav {
		display: none;
	}
	.hamburger {
		display: flex;
	}
	header {
		flex-direction: row;
	}
}

@media only screen and (max-width: 640px) {
	.logo {
		font-size: 42px;
	}
	.hamburger {
		width: 60px;
		height: 25px;
	}
}

@media only screen and (max-width: 500px) {
	.logo {
		font-size: 35px;
	}
	.hamburger {
		width: 50px;
		height: 20px;
	}

	.App {
		padding: 0px 10px;
	}
}

@media only screen and (max-width: 360px) {
	.logo {
		font-size: 31px;
	}
	.hamburger {
		width: 40px;
		height: 20px;
	}

	.App {
		padding: 0px 10px;
	}
}
