section {
	min-height: 300px;
	z-index: 2;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

ul,
li,
a {
	text-decoration: none;
	list-style: none;
	color: inherit;
}

html,
body,
div {
	font-family: 'Interstate';
}

.App {
	position: relative;
	width: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0px 23px;
	box-sizing: border-box;
}
