footer {
	display: flex;
	flex-direction: column;
	width: 100%;

	justify-content: flex-start;
	margin-top: 50px;
	margin-bottom: 10px;
	opacity: 0;
}

.mw {
	max-width: 1200px;
}

.footer-1st {
	padding-top: 9px;
	border-top: solid 1px rgba(0, 0, 0, 0.76);
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	max-width: 350px;
	margin-bottom: 20px;
	font-size: 17px;
	font-weight: 400;
	text-transform: uppercase;
}

.footer-2nd {
	font-size: 15px;
	font-weight: 300;
}

.footer-1st > div:hover {
	opacity: 0.8;
	cursor: pointer;
}

@media only screen and (max-width: 500px) {
	footer {
		margin-top: 15px;
	}

	.footer-1st {
		font-size: 13px;
	}

	.footer-2nd {
		font-size: 11px;
	}
}
